import React, { useEffect, useRef, useState } from "react";
import styled, { keyframes } from "styled-components";
import { modalPortalID } from "../Siblings/portalID";
import { Portal } from "../Siblings";

export const defaultDuration = 200;

interface IModalProps {
  open?: boolean;
  /** content */
  children?: React.ReactNode;
  /** click function on click background layer */
  onBGClick?: () => void;
  /**
   * cancel background layer
   * @default false
   */
  disableBG?: boolean;
  /**
   * which root div will be considered as modal's parent
   * @default ModalPortalID "orthogonal-modals"
   */
  portalID?: string;
  /**
   * display & dismiss animation duration
   * @unit million seconds
   * @default 200
   */
  duration?: number;
  style?: React.CSSProperties;
  backgroundStyle?: React.CSSProperties;
  /** replace default bg color (rgba(0, 0, 0, 0.4)) */
  backgroundColor?: string;
}

/**
 * @example
 * export function DemoModal() {
 *   const [open, setOpen] = useState(true);
 *   return (
 *     <Modal open={open} onBGClick={setOpen.bind(null, false)}>
 *       <>111</>
 *     </Modal>
 *   );
 * }
 */
export function Modal({
  open,
  children,
  onBGClick,
  disableBG,
  portalID = modalPortalID,
  duration = defaultDuration,
  style,
  backgroundColor,
  backgroundStyle,
}: IModalProps) {
  const wrapperDomRef = useRef<HTMLDivElement>(null);
  const { _open, enter, leave } = useModalEffect(
    !!open,
    duration,
    wrapperDomRef.current
  );
  if (!_open || !children) return null;
  return (
    <Portal id={portalID}>
      <ModalWrapper
        ref={wrapperDomRef}
        enter={enter}
        leave={leave}
        duration={duration}
        style={style}
      >
        {!disableBG && (
          <ModalBG
            enter={enter}
            leave={leave}
            onClick={onBGClick}
            duration={duration}
            style={backgroundStyle}
            backgroundColor={backgroundColor}
          />
        )}
        {attachProps(children, { enter, leave })}
      </ModalWrapper>
    </Portal>
  );
}

export const attachProps = (children?: React.ReactNode, props?: object) => {
  if (!children || !props) return children;
  if (!React.isValidElement(children)) return children;
  if (children instanceof Array) {
    return children.map((child) => React.cloneElement(child, props));
  }
  return React.cloneElement(children, props);
};

export function useModalEffect(
  open: boolean,
  duration = defaultDuration,
  dom?: HTMLDivElement | null
) {
  const [_open, _setOpen] = useState(open);
  const [_enter, _setEnter] = useState(false);
  const [_leave, _setLeave] = useState(false);
  useEffect(() => {
    if (open) _setOpen(true);
  }, [open]);
  useEffect(() => {
    if (_open && !open) _setLeave(true);
  }, [open, _open]);
  useEffect(() => {
    if (_open) _setEnter(true);
    if (!_open) _setEnter(false);
  }, [_open]);
  useEffect(() => {
    // leave
    if (_leave) {
      if (dom) {
        Promise.all(
          dom.getAnimations({ subtree: true }).map((a) => a.finished)
        ).then(() => {
          _setOpen(false);
          _setLeave(false);
        });
      } else {
        const timer = window.setTimeout(() => {
          _setOpen(false);
          _setLeave(false);
        }, duration);
        return () => clearTimeout(timer);
      }
    }
  }, [_leave, dom, duration]);
  useEffect(() => {
    // clear
    return () => {
      _setLeave(false);
      _setOpen(false);
    };
  }, []);
  return {
    enter: _enter,
    leave: _leave,
    _open,
  };
}

interface AnimProps {
  enter?: boolean;
  leave?: boolean;
  duration?: number;
  backgroundColor?: string;
}

function getAnimBG(p: AnimProps) {
  if (p.leave) return "transparent";
  if (p.enter) return p.backgroundColor || "rgba(0, 0, 0, 0.15)";
  return "transparent";
}

function getAnimOpacity(p: AnimProps) {
  if (p.leave) return 0;
  if (p.enter) return 1;
  return 0;
}

export const fadeIn = keyframes`
  0% { opacity: 0; }
  100% { opacity: 1; }
`;

export const ModalWrapper = styled.div<AnimProps>`
  ${(p) => p.theme.fixedFill}
  z-index: ${(p) => p.theme.zIndexes.modal};
  ${(p) => p.theme.flexCSS.column}
  ${(p) => p.theme.flexCSS.center}
  opacity: ${(p) => getAnimOpacity(p)};
  animation: ${(p) => p.duration}ms ${fadeIn} ease;
  transition: opacity ${(p) => p.duration}ms ease;
`;

export const ModalBG = styled.div<AnimProps>`
  ${(p) => p.theme.absoluteFillCSS}
  background-color: ${(p) => getAnimBG(p)};
  transition: background-color ${(p) => p.duration}ms ease;
  touch-action: none;
`;
